import Vue from 'vue'
import Router from 'vue-router'
// import BanChe from '../components/BanChe'
// import BanCheDetail from '../components/BanCheDetail'
// import Transportation from '../components/Transportation'
// import EmMeasures from '../components/EmMeasures'
Vue.use(Router)

// import { resolve } from 'path';
const Transportation = () => import('@/components/Transportation')
const BanChe = () => import('@/components/BanChe')
const BanCheDetail = () => import('@/components/BancheDetail')
const EmMeasures = () => import('@/components/EmMeasures')

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Transportation',
      component: Transportation
    },
    {
      path: '/yunshu',
      name: 'Transportation',
      component: Transportation
    },
    {
      path: '/banche',
      name: 'Banche',
      component: BanChe
    },
    {
      path: '/banche-detail',
      name: 'BancheDetail',
      component: BanCheDetail
    },
    {
      path: '/yingji',
      name: 'EmMeasures',
      component: EmMeasures
    }
  ]
})



