<template>
  <div class="BanChe">
    <el-card class="box-card" v-for="banche in bancheInfo" :key="banche.name" @click="goDetail(banche.name)">
      <div slot="header" class="clearfix">
        <strong>{{ banche.name }}每日班车时刻表</strong>
        <el-button class="more" style="float: right; padding: 3px 0" icon="el-icon-arrow-right" @click="goDetail(banche.name)"></el-button>
      </div>
      <div class="start">
        <MyIcon type="el-iconcurr_loc" class="point-icon"></MyIcon>
        <span>{{ banche.start }} ({{ banche.en_start }})</span>
      </div>
      <div class="end">
        <MyIcon type="el-iconqidian" class="point-icon"></MyIcon>
        <span>{{ banche.end }} ({{ banche.en_end }})</span>
      </div>
    </el-card>
  </div>
</template>
<script>
  import { Message } from 'element-ui';
  export default {
    name: "BanChe",
    data(){
      return{
        bancheInfo: null
      }
    },
    created(){
      this.$axios({
        method: 'get',
        url: 'static/data/banche.json'
      }).then((response) => {
        this.bancheInfo = response.data;
      }).catch((error) => {
        console.log(error)
      })
    },
    methods: {
      goDetail: function (name) {
        this.$router.push({path: '/banche-detail', query: {name}})
      },
    }
  }
</script>

<style>
  .more{
    border: 0;
  }
  .BanChe{
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    align-items:center;
    width: 100%;
    font-size: 13px;
  }
  .BanChe strong{
    font-size: 15px;
  }
  .dash-border{
    height: 20px;
    width: 100px;
    border-left:1px dashed rgb(204, 204, 204);
    margin-left: -0.115rem;
    margin-top: -0.1rem;
    margin-bottom: 0.2rem;
  }
  .point-icon{
    margin-top: 1rem;
    margin-left: -0.5rem;
    margin-right: 0.25rem;
  }
  .box-card{
    width: 90%;
    margin-top: 0.75rem;
    margin-bottom: 0.75rem;
  }
  .start{
    margin-top: -1rem;
  }
</style>



