<template>
  <div class="BanCheDetail">
    <div class="sec">
      <div class="sec-title">{{ this.bancheDetail.name }}每日运输计划</div>
      <p>{{ this.bancheDetail.plan }}</p>
    </div>
    <div class="sec">
      <div class="sec-title">组织路线图</div>
      <div class="map-box">
<!--        <img src="../assets/pxzx.png"/>-->
        <Map width="100%" height="300px" route-name="finish2xinhua" zoom="10"/>
      </div>
    </div>
    <div class="sec">
      <div class="sec-title">
        {{ this.bancheDetail.name }}班车时刻表
        <el-button id="save-all" type="primary" icon="el-icon-download" size="mini" @click="saveAllEvent">全部导入系统日历</el-button>
      </div>
      <el-table class="data-table" :data="tableData" stripe style="width: 100%">
        <el-table-column prop="depTime" label="发车时间">
          <template slot-scope="scope">
            <span>{{ scope.row.depTime }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="start" label="起点" align="center"/>
        <el-table-column prop="end" label="终点" align="center"/>
        <el-table-column prop="end" label="导入" align="center">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-date" size="mini" @click="saveEvent(scope.$index, scope.row)" circle plain></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--      <p>10月24日-31日提供酒店往返场馆班车，车辆在酒店客房楼和场馆出发区或结束区上落客。</p>-->
    </div>
  </div>
</template>

<script>
  import * as FileUtil from '@/utils/FileUtil'
  import Map from './Map'
  export default {
    name: 'BanCheDetail',
    components: { Map },
    data () {
      return {
        bancheInfo: null,
        bancheDetail: null,
        tableData: []
      }
    },
    created () {
      const curTime = this.$moment();
      this.$axios({
        method: 'get',
        url: 'static/data/banche.json'
      }).then((response) => {
        this.bancheInfo = response.data;
        for (let banche of response.data) {
          if (banche.name === this.$route.query.name) {
            this.bancheDetail = banche;
            break;
          }
        }
        for (let banci of this.bancheDetail.banci) {
          for (let arriTime of banci.arri_time) {
            if (curTime.isBefore(this.$moment(arriTime, 'YYYY-MM-DD HH:mm'))){
              let arri = arriTime.split("-");
              arri = arri[arri.length - 1].replace(" ", "日");
              this.tableData.push({
                "start": this.bancheDetail.start,
                "end": this.bancheDetail.end,
                "depTime": arri
              })
            }
          }
          for (let backTime of banci.back_time) {
            if (curTime.isBefore(this.$moment(backTime, 'YYYY-MM-DD HH:mm'))) {
              let back = backTime.split("-");
              back = back[back.length - 1].replace(" ", "日");
              this.tableData.push({
                "start": this.bancheDetail.end,
                "end": this.bancheDetail.start,
                "depTime": back
              })
            }
          }
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    methods: {
      saveEvent (index, row) {
        let myDate = new Date(2020, 10 - 1, parseInt(row.depTime.split("日")[0]));
        myDate.setHours(parseInt(row.depTime.split("日")[1].split(":")[0]), parseInt(row.depTime.split("日")[1].split(":")[1]), 0, 0)
        const startDateTime = FileUtil.dateFormat("YYYYmmddTHHMMSS", myDate);
        myDate.setMinutes(myDate.getMinutes() + 45);
        const endDateTime = FileUtil.dateFormat("YYYYmmddTHHMMSS", myDate);
        const loc = row.start;
        const title = row.depTime + '乘班车由' + row.start + '至' + row.end;
        const desc = this.bancheDetail.name + '可于' + row.depTime + '乘坐班车从' + row.start + '出发至' + row.end + '，预计到达时间' + endDateTime + '，耗时45分钟。';
        const ob = {
          "startTime": startDateTime,
          "endTime": endDateTime,
          "title": title,
          "loc": loc,
          "desc": desc
        };
        FileUtil.createAndDownloadFile("点我导入系统日历.ics", FileUtil.getCalendarFunction([ob]))
      },
      saveAllEvent(){
        console.log('save all');
        let obA = []
        for (let i = 0; i < this.tableData.length; i++) {
          let row = this.tableData[i];
          let myDate = new Date(2020, 10 - 1, parseInt(row.depTime.split("日")[0]));
          myDate.setHours(parseInt(row.depTime.split("日")[1].split(":")[0]), parseInt(row.depTime.split("日")[1].split(":")[1]), 0, 0)
          const startDateTime = FileUtil.dateFormat("YYYYmmddTHHMMSS", myDate);
          myDate.setMinutes(myDate.getMinutes() + 45);
          const endDateTime = FileUtil.dateFormat("YYYYmmddTHHMMSS", myDate);
          const loc = row.start;
          const title = row.depTime + '乘班车由' + row.start + '至' + row.end;
          const desc = this.bancheDetail.name + '可于' + row.depTime + '乘坐班车从' + row.start + '出发至' + row.end + '，预计到达时间' + endDateTime + '，耗时45分钟。';
          const ob = {
            "startTime": startDateTime,
            "endTime": endDateTime,
            "title": title,
            "loc": loc,
            "desc": desc
          };
          obA.push(ob);
        }
        FileUtil.createAndDownloadFile("点我导入系统日历.ics", FileUtil.getCalendarFunction(obA))

      }

    }
  }
</script>

<style>
  .BanCheDetail{
    /*background-color: white;*/
  }
  /*.BanCheDetail table{*/
  /*  text-align: center;*/
  /*}*/
  .sec{
    width: 95%;
    margin: 0 auto;
  }
  .sec span{
    line-height: 0.5rem;
    margin-top: 0.5rem !important;
  }
  .sec p{
    text-indent: 20px;
    font-size: 13px;
    padding-top: 0.2rem;
    margin-bottom: -0.05rem;
  }
  .sec-title{
    font-weight: bold;
    font-size: 14px;
    color: #065bc6;
    padding-top: 0.5rem;
    padding-bottom: 0.4rem;
  }
  .map-box{
    display: flex;
    /*height: 150px;*/
    margin-bottom: 0.5rem;
  }
  .map-box img{
    width: 50%;
  }
  .BanCheDetail .data-table{
    padding-bottom: 1rem;
  }
  .point-icon{
    margin-left: 0.5rem;
    line-height: 2px;
  }
  #save-all{
    float: right;
    margin-bottom: 0.5rem;
    margin-top: -0.2rem;
  }
  p{
    color: #333333;
  }
</style>



