export function createAndDownloadFile(fileName, content) {
  let aTag = document.createElement('a');
  let blob = new Blob([content]);
  aTag.download = fileName;
  aTag.href = URL.createObjectURL(blob);
  let a = document.createElement('a');
  a.setAttribute('rel', 'noreferrer');
  a.setAttribute('id', 'm_noreferrer');
  a.setAttribute('href', aTag.href);
  a.setAttribute('download', fileName);

  document.body.appendChild(a);
  document.getElementById('m_noreferrer').click();
  document.body.removeChild(a);
  // aTag.click();
  URL.revokeObjectURL(blob);
}

export function getCalendarFunction (objectArray) {
  let str = 'BEGIN:VCALENDAR\n' +
    'PRODID:-//Microsoft Corporation//Outlook 16.0 MIMEDIR//EN\n' +
    'VERSION:2.0\n' +
    'METHOD:REQUEST\n' +
    'X-MS-OLK-FORCEINSPECTOROPEN:TRUE\n';
  for (let eventObject of objectArray){
    let eventStr =
      'BEGIN:VEVENT\n' +
      'DESCRIPTION: ' + eventObject.desc + '\n' +
      'DTSTART;' +
      'TZID="China/Beijing":' + eventObject.startTime + '\n' +
      'DTEND;TZID="China/Beijing":' + eventObject.endTime + '\n' +
      'LOCATION;LANGUAGE=zh-cn:' + eventObject.loc + '\n' +
      'SUMMARY;LANGUAGE=zh-cn:' + eventObject.title.replace(' ') + '\n' +
      'X-MICROSOFT-CDO-BUSYSTATUS:TENTATIVE\n' +
      'X-MICROSOFT-CDO-IMPORTANCE:1\n' +
      'X-MICROSOFT-CDO-INTENDEDSTATUS:BUSY\n' +
      'X-MICROSOFT-DISALLOW-COUNTER:FALSE\n' +
      'X-MS-OLK-CONFTYPE:0\n' +
      'BEGIN:VALARM\n' +
      'TRIGGER:-PT15M\n' +
      'ACTION:DISPLAY\n' +
      'DESCRIPTION:Reminder\n' +
      'END:VALARM\n' +
      'END:VEVENT\n';
    str = str + eventStr;
  }
  str = str + 'END:VCALENDAR\n';
  return str;
}

export function dateFormat(fmt, date) {
  let ret;
  const opt = {
    "Y+": date.getFullYear().toString(),        // 年
    "m+": (date.getMonth() + 1).toString(),     // 月
    "d+": date.getDate().toString(),            // 日
    "H+": date.getHours().toString(),           // 时
    "M+": date.getMinutes().toString(),         // 分
    "S+": date.getSeconds().toString()          // 秒
    // 有其他格式化字符需求可以继续添加，必须转化成字符串
  };
  for (let k in opt) {
    ret = new RegExp("(" + k + ")").exec(fmt);
    if (ret) {
      fmt = fmt.replace(ret[1], (ret[1].length === 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
    }
  }
  return fmt;
}

