<template>
  <div id="EM">
    <strong>疫情通知模板</strong>
    <el-input class="yiqing-input"
              type="textarea"
              :rows="8"
              placeholder="请输入内容"
              v-model="textarea">
    </el-input>
    <el-button class="yiqing-button" type="primary" icon="el-icon-document-copy" size="mini"
               v-clipboard:copy="textarea" @click="onCopy()">
      复制
    </el-button>
    <strong>应急调控措施</strong>
    <a-collapse :bordered="false" expandIconPosition="right">
      <template #expandIcon="props">
        <a-icon type="right" :rotate="props.isActive ? 90 : 0" />
      </template>
      <a-collapse-panel key="1" header="车辆行驶故障" :style="customStyle">
        <p><Steps v-bind:content="textList.A"></Steps></p>
      </a-collapse-panel>
      <a-collapse-panel key="2" header="车辆失火" :style="customStyle">
        <p><Steps v-bind:content="textList.B"></Steps></p>
      </a-collapse-panel>
      <a-collapse-panel key="3" header="外围交通事故" :style="customStyle">
        <p><Steps v-bind:content="textList.C"></Steps></p>
      </a-collapse-panel>
      <a-collapse-panel key="4" header="场馆内发生交通事故" :style="customStyle">
        <p><Steps v-bind:content="textList.D"></Steps></p>
      </a-collapse-panel>
      <a-collapse-panel key="5" header="场馆停车位不足" :style="customStyle">
        <p><Steps v-bind:content="textList.E"></Steps></p>
      </a-collapse-panel>
      <a-collapse-panel key="6" header="场馆周边道路突然塌陷" :style="customStyle">
        <p><Steps v-bind:content="textList.F"></Steps></p>
      </a-collapse-panel>
      <a-collapse-panel key="7" header="因人群聚集造成交通阻塞" :style="customStyle">
        <p><Steps v-bind:content="textList.G"></Steps></p>
      </a-collapse-panel>
    </a-collapse>
  </div>
</template>
<script>
import Steps from "@/components/Steps.vue";
import { Message } from 'element-ui'
export default {
  components: {
    Steps,
  },
  data() {
    return {
      textarea: '　　国家雪车雪橇中心场地预认证工作计划于10月26日开始，日前XXX（男，X岁，XX国运动员）在抵达赛区定点酒店进行赛前核酸检测时，核酸检测呈阳性，经确诊感染新冠肺炎，现已送入XXX定点医院进行治疗，情况平稳，与其密切接触者也已经接受医学隔离观察。\n' +
        '　　相关部门正在密切沟通，研究评估是否继续进行预认证工作，并将及时进行发布。',
      text: `A dog is a type of domesticated animal.Known for its loyalty and faithfulness,it can be found as a welcome guest in many households across the world.`,
      customStyle:
        'background: #f7f7f7;border-radius: 4px;margin-bottom: 24px;border: 0;overflow: hidden',
      //风险应对列表
      textList: {
        A: {
          '步骤 1':'视故障情况，条件许可时将车辆停在安全地点',
          '步骤 2':'确认无法行驶时，将车上乘客安全疏导到后续车辆',
          '步骤 3':'向交通指挥监控室发出求助信号',
          '步骤 4':'现场指挥调用救援车，并指挥交通组织管理人员对道路进行管控',
          '步骤 5':'将故障车拖离道路或及时修复',
          '步骤 6':'完成'
          },
        B: {
          '步骤 1':'迅速疏导乘车人离车至安全地带，并向场馆礼宾业务口报送相关情况',
          '步骤 2':'同时上报交通指挥监控室、场馆运行中心和场馆交通经理',
          '步骤 3':'利用随车灭火器实施火险扑救措施',
          '步骤 4':'视情况通知消防车赶赴现场实施灭火',
          '步骤 5':'交通运行中心调度应急车辆运送乘车人员至指定安全场所',
          '步骤 6':'完成'
        },
        C: {
          '步骤 1':'立即参与人员救助，通知救护车和救援车辆前往事故发生地',
          '步骤 2':'迅速上报交通指挥监控室、场馆运行中心和场馆交通经理',
          '步骤 3':'调派警力、采取临时交通管理措施，疏导场馆内交通，保障救援通道畅通',
          '步骤 4':'人员救助后，由清障车拖走事故车，清理事故现场，争取快速腾清现场',
          '步骤 5':'完成'
        },
        D: {
          '步骤 1':'启用备用停车场（可利用外围道路或外围停车场）',
          '步骤 2':'增加交通疏导工作人员',
          '步骤 3':'完成'
        },
        E: {
          '步骤 1':'立即报告交通指挥监控室、场馆运行中心和场馆交通经理',
          '步骤 2':'场馆交通经理协调相关业务口，加大宣传疏导力量，迅速腾清路面',
          '步骤 3':'视情况指挥车辆利用应急备用行车路线疏散',
          '步骤 4':'完成'
        },
        F: {
          '步骤 1':'立即报告交通指挥监控室、场馆运行中心和场馆交通经理',
          '步骤 2':'场馆交通经理协调相关业务口，加大宣传疏导力量，迅速腾清路面',
          '步骤 3':'视情况指挥车辆利用应急备用行车路线疏散',
          '步骤 4':'完成'
        },
        G: {
          '步骤 1':'交通团队人员应本着以人为本、服务至上的原则，避免与服务对象发生直接冲突',
          '步骤 2':'对无法解决的问题，应请示交通经理或服务副经理，通过各利益相关方所在业务口协调处理',
          '步骤 3':'完成'
        }
      }
    };
  },
  methods: {
    onCopy: function () {
      Message({
        message: '复制成功',
        type: 'success'
      });
    }
  }
};
</script>
<style>
  #EM{
    /*display: block;*/
    display: flex;
    flex-direction: column ;
    justify-content: center;
  }
  .yiqing-input{
    /*height: 12rem;*/
    width: 95%;
    margin: 0 auto;
  }
  .yiqing-button{
    width: 30%;
    margin: 0 auto;
    margin-top: 0.5rem;
    /*float: right;*/
  }
  #EM strong{
    padding-top: 0.5rem;
    padding-left: 0.5rem;
    padding-bottom: 0.5rem;
    /*color: #065bc6;*/
    font-size: 15px;
  }
</style>



