
<template>
  <div id="container" ref="AmapContainer"></div>
</template>

<script>
  import AMapLoader from '@amap/amap-jsapi-loader';
  export default {
    name: 'Map',
    data () {
      return {
        routeInfo: '',
        map: '',
      }
    },
    props: {
      width: {
        type: String,
        default: () => '100%'
      },
      height: {
        type: String,
        default: () => '250px'
      },
      routeName: {
        type: String,
        default: () => 'sdjc2xinhua'
      },
      zoom: {
        type: String,
        default: () => '9'
      }
    },
    methods: {

    },
    created () {

    },
    mounted () {
      this.$refs.AmapContainer.style.setProperty("width", this.width);
      this.$refs.AmapContainer.style.setProperty("height", this.height);
      this.$axios({
        method: 'get',
        url: 'static/data/route.json'
      }).then((response) => {
        let map = new AMap.Map('container',{
          mapStyle: 'amap://styles/f82ec60b29dbd494e3d91a32971ef169', //设置地图的显示样式
        });
        AMap.plugin([
          'AMap.ToolBar'
        ], function(){
          map.addControl(new AMap.ToolBar({
            position: "RT",
            locate: true
          }));
        });
        this.routeInfo = response.data[this.routeName];
        let line_pos = this.routeInfo.split(";");
        let path = [];
        for (let fu of line_pos) {
          path.push(new AMap.LngLat(parseFloat(fu.split(',')[0]), parseFloat(fu.split(',')[1])))
        }
        let polyline = new AMap.Polyline({
          path: path,
          borderWeight: 1, // 线条宽度，默认为 1
          strokeColor: 'red', // 线条颜色
          lineJoin: 'round', // 折线拐点连接处样式
          strokeStyle: 'dashed', //设置路线为虚线
          strokeDasharray: [7,2],   //虚线样式
        });
        // 将折线添加至地图实例
        map.add(polyline);
        map.setFitView(polyline);
        map.setZoom(parseInt(this.zoom))
        this.$axios({
          method: 'get',
          url: 'static/data/point.json'
        }).then((response) => {
          const pointList = response.data;
          for (let point of pointList){
            if (point.isMarker){
              let amapText = new AMap.Text({
                text: point.name,
                position: [point.lng, point.lat],
              });
              map.add(amapText);
              let amapMarker = new AMap.CircleMarker({
                center:[point.lng, point.lat],
                radius:5,
                strokeColor:'white',
                strokeWeight:2,
                strokeOpacity:1,
                fillColor:'green',
                fillOpacity:0.8,
                zIndex:10,
                bubble:true,
                cursor:'pointer',
                clickable: true
              })
              amapMarker.setMap(map)
            }
          }
        }).catch((error) => {
          console.log(error)
        })
      }).catch((error) => {
        console.log(error)
      })
    }
  }
</script>
<style scoped>
  #container{
    height: 250px;
    width: 100%;
  }
  .amap-overlay-text-container{
    background: rgba(0, 0, 0, 0);
  }
  .amap-logo{
    display: none !important;
    opacity:0 !important;
  }
  .amap-copyright {
    opacity:0 !important;
  }
</style>



