<template>
  <div>
    <a-steps v-model="current" direction="vertical" @change="onChange">
      <a-step :title="key" :description="con" v-for="(con,key,index) in content" :key="key" />
    </a-steps>
  </div>
</template>
<script>
export default {
  props: {
    content: Object
  },
  data() {
    return {
      current: 0,
    };
  },
  methods: {
    onChange(current) {
      console.log('onChange:', current);
      this.current = current;
    },
  },
};
</script>



