<template>
  <div id="app" >
    <div id="header">
        <img id="logo" src="@/assets/logo2.png" alt="logo" @click="$router.push({path: '/'})">
        <img id="fanyi" src="@/assets/fanyi.png" alt="fanyi">
        <img id="caidan" src="@/assets/caidan.png" alt="caidan" @click="drawer = true">
<!--      <el-col :span="1" class="header-col">-->
<!--      </el-col>-->
    </div>
    <el-drawer
      title=""
      :visible.sync="drawer"
      :direction="direction"
      :with-header="true"
      size=	'50%' >
      <el-menu
        default-active="/yunshu"
        route="true"
        class="el-menu-vertical-demo"
        background-color="#1b448e"
        text-color="#fff"
        active-text-color="#ffd04b">
        <router-link to="/yunshu" >
          <el-menu-item index="/yunshu" @click="setTitle('关闭抽屉')">
            <i class="el-icon-truck"></i>
            <span slot="title">交通运输任务</span>
          </el-menu-item>
        </router-link>
        <router-link to="/banche">
          <el-menu-item index="/banche" @click="setTitle('关闭抽屉')">
            <i class="el-icon-time"></i>
            <span slot="title">每日运输计划</span>
          </el-menu-item>
        </router-link>
        <router-link to="/yingji">
          <el-menu-item index="/yingji" @click="setTitle('关闭抽屉')">
            <i class="el-icon-warning-outline" ></i>
            <span slot="title">应急调控措施</span>
          </el-menu-item>
        </router-link>
      </el-menu>
    </el-drawer>
<!--    <el-row :gutter="24" class="fake-header"></el-row>-->
    <div id="main-img"></div>

<!--    <img src="./assets/xcxqzx.png" alt="xcxqzx" id="main-img">-->
    <div id="title-bar">
      <span>
        <strong>
          <MyIcon class="back-icon" @click="$router.go(-1)" type="el-icon_back"></MyIcon>{{ titleName }}
        </strong>
      </span>
    </div>
    <div id="main">
      <router-view/>
    </div>
    <div id="footer">
      <div id="lab-logo">
        <img src="@/assets/lab-gray.png">
      </div>
      <div id="detail">
        <p>地址：北京市海淀区学院路37号</p>
        <p>邮编：100191 电话：82317114</p>
        <p>版权所有：2020 “一张票”关键技术项目组</p>
        <p>京ICP备XXXX 文保网安备案号XXXX</p>
      </div>
    </div>
<!--    <el-row :gutter="24" id="main-image">-->
<!--    </el-row>-->
<!--    <el-row :gutter="24" type="flex" id="title" justify="center">-->
<!--&lt;!&ndash;      <el-col :span="2">交通运输任务</el-col>&ndash;&gt;-->
<!--      <el-col :span="20" class="title-col">-->
<!--        <span>交通运输任务</span>-->
<!--      </el-col>-->
<!--&lt;!&ndash;      <el-col :span="2">交通运输任务</el-col>&ndash;&gt;-->
<!--    </el-row>-->
<!--    <el-row id="main">-->

<!--    </el-row>-->

<!--    <el-row id="footer">-->


<!--    </el-row>-->

  </div>
</template>

<script>
  import BanChe from './components/BanChe'
  import BanCheDetail from './components/BanCheDetail'
  import EmMeasures from './components/EmMeasures'
  import Transportation from './components/Transportation'
  import * as FileUtil from '@/utils/FileUtil'
  import { Loading } from 'element-ui';
  export default {
    name: 'App',
    components: { Transportation, EmMeasures, BanChe, BanCheDetail },
    data () {
      return {
        drawer: false,
        direction: 'rtl',
        titleName: '交通运输任务'
      }
    },
    created () {
      if (FileUtil.isWeChat()){
        const loading = this.$loading({
          lock: true,
          text: '请点击右上角，使用浏览器打开',
          spinner: 'el-icon-error',
          background: 'rgba(0, 0, 0, 0.7)'
        });
        setTimeout(() => {
          loading.close();
        }, 122000);
      }
    },
    beforeUpdate () {
      console.log(this.$route.path, (this.$route.path.indexOf('banhce')) === -1 )
      // console.log(this.$route.path)
      if(this.$route.path === '/banche' ||  this.$route.path === '/banche-detail' ){
        this.titleName = '每日运输计划'
      }
      if(this.$route.path === '/' ||  this.$route.path === '/yunshu' ){
        this.titleName = '交通运输任务'
      }
      if(this.$route.path === '/yingji'){
        this.titleName = '应急调控方案'
      }
    },
    methods: {
      setTitle: function (titleName) {
        this.drawer = false;
      }
    }
  }
</script>

<style>
  #app{
    font-family: "Heiti SC";
    overflow-x: hidden;
    /*display: block;*/
    /*text-align: center;*/
  }
  #header{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 6%;
    background-color: white;
    z-index: 1000;
    margin: 0 !important;
    padding: 0;
    display: flex;
    justify-content: space-between;

  }

  #logo{
    padding-left: 0.5rem;
    align-self: center;
    width: 14rem;
  }
  #fanyi{
    /*margin-left: 4.1rem;*/
    align-self: center;
    margin-right: -2em;

    width: 1.6rem;
  }
  #caidan{
    margin-right: 1.5em;
    align-self: center;
    width: 1.6rem;
  }
  #main-img{
    margin-top: 3rem;
    margin-left: -0.9rem;
    margin-right: -0.45rem;
    /*width: 80%;*/
    background-image: url("../assets/xcxqzx.png");
    background-size: 100% 100%;
    /*width: 100%;*/
    height: 7rem;
  }
  #title-bar{
    background-image: url("../assets/buaa.jpg");
    background-size: 100% 100%;
    margin-top: -1.1rem;
    margin-left: 1rem;
    margin-right: 1rem;
    /*width: 80%;*/
    /*margin-left: 0rem;*/
    height: 2.6rem;
  }
  #title-bar span{
    display: table-cell;
    line-height: 2.6rem;
    font-size: 1.0rem;
    color: #fff;
    padding-left: 0.8rem;
  }
  #footer {
    background-color: #333333;
    color: #333;
    height: 50%;
    /*width: 110%;*/
    /*margin-left: -0.9rem;*/
    /*margin-right: -0.45rem;*/

  }
  #lab-logo{
    width: 100%;
    margin-bottom: 0.5rem;
    border-bottom: 0.02rem dashed #6e6e6e;
  }
  #lab-logo img{
    margin-left: 1rem;
    margin-top: 0.5rem;
    height: 2.5rem;
  }
  #detail p{
    margin-left: 1rem;
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.8);
    width: 100%;
    line-height: 1.7rem;
    margin-bottom: 0 !important;
  }
  #main{
    background-color: rgb(242, 246, 249);
  }
  /*#main {*/
  /*  background-color: #E9EEF3;*/
  /*  color: #333;*/
  /*  text-align: center;*/
  /*  position: absolute;*/
  /*  top:50px*/
  /*}*/
  #App .el-drawer.ltr, .el-drawer.rtl, .el-drawer__container{
    height: 197px;
  }
  .el-drawer__header{
    margin: 0;
    padding: 0;
    padding-right: 0.5rem;
    background-color: #1b448e;
  }
  .back-icon{
    margin-right: 0.7rem;
  }
  /*.el-drawer__header i{*/
  /*  padding-left: -1rem;*/
  /*}*/
</style>



